@import "../../style.scss";
.leftbar {
	flex: 1.3;
	// border: 1px lightgray solid;
	box-shadow: 5px 0px 8px 3px rgba(0, 0, 0, 0.05);
	position: sticky;
	top: 70px;
	height: calc(100vh - 70px);
	overflow: scroll;
	@include mobile {
		display: none;
	}
	&::-webkit-scrollbar {
		display: none;
	}
	.container {
		padding: 20px;
		hr {
			margin: 20px 0px;
			border: none;
			height: 0.5px;
			background-color: lightgray;
		}
		.menu {
			display: flex;
			flex-direction: column;
			gap: 20px;
			span {
				font-size: 12px;
			}
			.user {
				display: flex;
				align-items: center;
				gap: 10px;
				img {
					width: 30px;
					height: 30px;
					border-radius: 50%;
					object-fit: cover;
				}
				span {
					font-size: 14px;
				}
			}
			.item {
				display: flex;
				align-items: center;
				gap: 10px;
				cursor: pointer;
				span {
					font-size: 14px;
				}
			}
		}
	}
}
